import React, { Component } from 'react';
import Errores from './errores/errores';
import { Col } from 'reactstrap';
import { Nav } from 'reactstrap';
import { NavItem } from 'reactstrap';
import { NavLink } from 'reactstrap';
import { Row } from 'reactstrap';
import { TabContent } from 'reactstrap';
import { TabPane } from 'reactstrap';
import $ from 'jquery';
import '../assets/css/index.css'
import Totales from './errores/totales';
export class Tabs extends Component {

    state = {
        errores: [],
        show: true,
        activeTab: new Array(4).fill('1'),
        medio: "FB"
    }
    async componentDidMount() {
        //const res = await fetch('https://annalectbox.com.gt/AdOps/ApiErrors/all.php?usuario=0&media=FB')
        const res = await fetch('https://annalectbox.com.gt/AdOps/Api/Errors/DataPlatforms/v1/Data/?token=AasdKAS12ksaKasa9zbG9zZXJyb3JlYWFMas912SaaszMjAxOQ==')
        const data = await res.json();
        this.setState({ errores: data })


    }

    activarfiltro(e) {
       
$("#enviofiltro").click();
        $("#filtroSolicitacoes").on("submit", function () {
            $(" .tab-pane  .contendedordelerror").show();
            var filter = "";
            $(this).find("[data-form]").each(function () {
                if ($(this).val() !== "") filter += ("[" + $(this).attr("data-form") + "='" + $(this).val() + "']");

            });
            if (filter.length > 0) $(" .tab-pane  .contendedordelerror").not(filter).hide();

            return false;

        })

    }


    toggle(tabPane, tab) {
        const newArray = this.state.activeTab.slice()
        newArray[tabPane] = tab
        this.setState({
            activeTab: newArray,
        });
    }

   
    tabPane() {
        return (
            <>
                <TabPane tabId="1">
                <Errores medio={this.state.medio} errores={this.state.errores} />
                </TabPane>
                <TabPane tabId="3">
                <Errores medio={this.state.medio} errores={this.state.errores} />
                </TabPane>
                <TabPane tabId="2">
                <Errores medio={this.state.medio} errores={this.state.errores} />
             
                </TabPane>
            </>
        );
    }
 
    render() {

        return (

            <div className="col-12 col-xl-11 col-md-11 m-auto">
        
<Totales />
                <div className=" m-auto col-12 col-xl-11 contendedorprincipalerrores">
               
                    <form id="filtroSolicitacoes" type="post" name="filtroSolicitacoes" className="container d-flex justify-content-around p-2 flex-wrap align-items-center col-xl-10 col-12">
                    <p className="m-0 col-12 col-xl-2 col-md-12">Filtrar Por:</p>
                        <select data-form="estado" className="custom-select col-12 col-xl-3 col-md-3 mt-2 mb-2" name="filtroStatus" id="filtroStatus" onChange={this.activarfiltro}>
                            <option value="">Tipo de Error</option>

                            <option value="Error de nomenclatura">Nomenclatura</option>
                            <option value="Error de ubicacion">Ubicacion</option>
                            <option value="Error de inversion">Inversión</option>

                        </select>
                        {/*
                        <select data-form="marca" className="custom-select col-12 col-xl-3 col-md-3 mt-2 mb-2" name="filtromarca" id="filtroAnalista">
                            <option value="">Marca</option>
                            <option value="Doritos_PEPSICO_OMD_BMGT_S1">Doritos</option>
                            <option value="Quaker_OMD_GT">QUAKER</option>
                            <option value="Guateprenda_OMD_GT">Guateprenda</option>

                        </select>*/}
                        <select data-form="tipouser" className="custom-select col-12 col-xl-3 col-md-3 mt-2 mb-2" name="filtroAnalista" id="filtroAnalista" onChange={this.activarfiltro}>
                            <option value="">Tipo de Usuario</option>
                            <option value="1">Planer</option>
                            <option value="2">Implementación</option>

                        </select>
                        <button type="submit" className="btn btn-primary col-12 col-xl-2 " onClick={this.activarfiltro} id="enviofiltro" hidden>Filtrar</button>
                    </form>


                </div>
                <Row className="col-xl-11 col-md-12 col-sm-12 col-12 ml-auto mr-auto mt-1 row filtrodetabs">
                <Col xs="12" md="12" className="mb-4">
                    <Nav tabs className="col-12 flex-wrap d-flex">
                        <NavItem  className="col-12 col-xl-2">
                            <NavLink className="p-1 fbcon"
                                active={this.state.activeTab[0] === '1'}
                                onClick={() => { this.toggle(0, '1'); this.setState({medio : "FB"}) } }
                            >
                            Facebook
                               <i className="fab fa-facebook-f ml-2"></i>
              </NavLink>
                        </NavItem>
                        <NavItem className="col-12 col-xl-2">
                            <NavLink className="p-1 gocon"
                                active={this.state.activeTab[0] === '2'}
                                onClick={() => { this.toggle(0, '2');  this.setState({medio : "GO"}) } }
                            >
                            Google
                               <i className="fab fa-google ml-2"></i>
              </NavLink>
                        </NavItem>
                        <NavItem className="col-12 col-xl-2">
                            <NavLink className="p-1 twcon"
                                active={this.state.activeTab[0] === '3'}
                                onClick={() => { this.toggle(0, '3');  this.setState({medio : "TW"}) }}
                            >
                            Twitter
                              <i className="fab fa-twitter ml-2"></i>
              </NavLink>
                        </NavItem>
                    </Nav>
                    <TabContent  activeTab={this.state.activeTab[0]}>
                        {this.tabPane()}
                    </TabContent>
                </Col>


            </Row>
            </div>

        )



    }
}

export default Tabs
