import React, { Component } from 'react';
import '../assets/css/nav.css';
import '../assets/fontawesome-free/css/all.min.css';
import $ from 'jquery';
 class Nav extends Component {

    sideaction(){
        $("#sidebarToggle, #sidebarToggleTop").click();
  $("#sidebarToggle, #sidebarToggleTop").click(function(e) {
    console.log("Asdfasd");
    $("body").toggleClass("sidebar-toggled");
    $(".sidebar").toggleClass("toggled");

  });
    }
    render(){
    return (

            <ul className="navbar-nav bg-gradient-primary sidebar sidebar-dark accordion toggled" id="accordionSidebar">
                <a className="sidebar-brand d-flex align-items-center justify-content-center" href="/">
                    <div className="sidebar-brand-icon rotate-n-15">
                        <i className="fas fa-laugh-wink"></i>
                    </div>
                    <div className="sidebar-brand-text mx-3">AdOps<sup>2</sup></div>
                </a>
                    <li className="nav-item">
                        <a className="nav-link" href="/test">
                            <i className="fas fa-fw fa-tachometer-alt"></i>
                            <span>Dashboard AdOps</span></a>
                    </li>
                        <div className="sidebar-heading">
                            Planning
    </div>
                        <li className="nav-item">
                            <a className="nav-link" href="https://annalectbox.com.gt/MFC/">
                                <i className="fas fa-chart-pie"></i>
                                <span>MFC</span></a>
                        </li>


                            <div className="sidebar-heading">
                                Operaciones
    </div>

                            <li className="nav-item active">
                                <a className="nav-link" href="https://annalectbox.com.gt/AdOps/PlataformErrores">
                                    <i className="fas fa-exclamation-triangle"></i>
                                    <span>Errores</span></a>
                            </li>

                            <li className="nav-item">
                                <a className="nav-link" href="/">
                                    <i className="fas fa-tasks"></i>
                                    <span>Tareas</span></a>
                            </li>

                            <li className="nav-item">
                                <a className="nav-link" href="/">

                                    <i className="fas fa-fw fa-chart-area"></i>
                                    <span>Resultados</span></a>
                            </li>


                                <div className="text-center d-none d-md-inline">
                                    <button className="rounded-circle border-0" id="sidebarToggle" onClick={this.sideaction}></button>
                                </div>

  </ul>
                        )
                    
                    }
                }
export default Nav;